</template>

<script>
import Alert from './components/AlertMessage'
export default {
  name: 'app',
  components: {
    Alert
  },
  data () {
    return {
      active: 0,
      pay_mode: '',
      credit_type: '',
      credit_form: {
        pay_date: '一次付款',
        credit_num1: '',
        credit_num2: '',
        credit_num3: '',
        credit_num4: '',
        pay_month: '',
        pay_year: '',
        pay_safe: ''
      },
      paid_bank: [
        { value: '台灣銀行', label: '台灣銀行' },
        { value: '國泰銀行', label: '國泰銀行' }
      ],
      paid_market: [
        { value: '7-11', label: '7-11' },
        { value: '全家', label: '全家' },
        { value: 'OK', label: 'OK' }
      ],
      bank: '',
      supermarket: '',
      checked: false,
      email: '',
      emailCheck: true
    }
  },
  computed: {
    payMode () {
      if (this.pay_mode === '超商付款' || this.pay_mode === 'Web') {
        return false
      } else {
        return true
      }
    }
  },
  methods: {
    nextStep () {
      // if (this.active < 3) {
      //   this.active++
      // }
      switch (this.active) {
        case 0:
          this.active++
          break
        case 1:
          if (!this.checked || !this.emailCheck || this.email === '') {
            this.$bus.$emit('message:push', 'Email不得為空，確認方塊請點選')
            break
          }
          if (this.pay_mode === '超商付款') {
            if (this.supermarket === '') {
              this.$bus.$emit('message:push', '請選擇超商')
            } else {
              this.active++
              this.reset()
            }
            break
          } else if (this.pay_mode === 'Web') {
            if (this.bank === '') {
              this.$bus.$emit('message:push', '請選擇銀行')
            } else {
              this.active++
              this.reset()
            }
            break
          } else {
            if (!Object.values(this.credit_form).some(val => val === '')) {
              this.active++
              this.reset()
            } else {
              this.$bus.$emit('message:push', '有欄位尚未填寫', 'danger')
            }
            break
          }
      }
    },
    backStep () {
      if (this.active > 0) {
        this.active--
      }
    },
    openInfo () {
      document.querySelector('.nav.mobile-nav').classList.toggle('active')
    },
    reset () {
      this.credit_form = {
        pay_date: '',
        credit_num1: '',
        credit_num2: '',
        credit_num3: '',
        credit_num4: '',
        pay_month: '',
        pay_year: '',
        pay_safe: ''
      }
      this.credit_type = ''
      this.pay_mode = ''
      this.bank = ''
      this.supermarket = ''
      this.checked = false
      this.email = ''
      this.emailCheck = true
    }
  },
  watch: {
    'credit_form.credit_num1' (value) {
      value = parseInt(value)
      const visa = new RegExp('^4[0-9]{3}$')
      const master = new RegExp('^5[0-9]{3}$')
      const jcb = new RegExp('^3528$')
      if (visa.test(value)) {
        this.credit_type = 'visa'
      } else if (master.test(value)) {
        this.credit_type = 'mastercard'
      } else if (jcb.test(value)) {
        this.credit_type = 'jcb'
      } else {
        this.credit_type = ''
      }
    },
    email (email) {
      const emailRule = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      if (emailRule.test(email)) {
        this.emailCheck = true
      } else {
        this.emailCheck = false
      }
    }
  },
  updated () {
    // credits autoFocus
    // const credits = document.querySelectorAll('.credit')
    // if (credits[3].value.length === 4) return;
    // credits.forEach((credit, index) => {
    //   if (index === 3) return;
    //   if (credit.value.length === 4) {
    //     credits[index + 1].focus()
    //   }
    // })
  }
}
</script>
